<script>
import cookie from '../../static/js/cookie'
import {Cell, Switch as Vswitch , Accordion} from 'vue-ydui'
export default {
  data () {
    return {
        gender:[],
        switch1: false,
        switch2: true,
        switch3: true,
        switch4: 'aaaaa',
        switch5: true,
    }
  },
  components: {
      Cell,
      Vswitch,
      Accordion
  },
  methods: {
      Loginout () {
          this.$router.push({path:'/login'})
      }
  }
}
</script>

<style lang="stylus" scoped>

